<template>
  <v-card>
    <v-card-subtitle>

    </v-card-subtitle>
    <v-card-text>
      <v-row dense>
        <v-col cols="12"
               sm="12">

          <mrc_ess_staffMsg :label="$t('label_The_acting_employee')+'*'"
                            :disabled='disabled'
                            v-model="params.agent_staff_uuid"
                            :placeholder="$t('label_Employee_name_number')"></mrc_ess_staffMsg>
        </v-col>
        <v-col cols="12"
               class="d-flex"
               sm="12">
          <mrc_ess_staffMsg :label="$t('label_Acting_employee')+'*'"
                            :disabled='disabled'
                            v-model="params.proxy_staff_uuid"
                            :placeholder="$t('label_Employee_name_number')"></mrc_ess_staffMsg>
        </v-col>
        <v-col cols="12"
               sm="12">
          <mrc_ess_prompt :rules="false"
                          v-model="params.flow_proxy_type_uuid"
                          :label="$t('label_The_type_of_business')+'*'"
                          :disabled='disabled'
                          :obj='agent_approval_type'>
          </mrc_ess_prompt>
        </v-col>
        <v-col class="d-flex"
               cols="12"
               sm="6">
          <mrc_ess_datePicker :label="$t('label_Start_date')+'*'"
                              :disabled='disabled'
                              v-model="params.effective_start"
                              placeholder='YYYY/MM/DD'></mrc_ess_datePicker>
        </v-col>
        <v-col cols="12"
               sm="5">
          <mrc_ess_datePicker :label="$t('label_End_date')+'*'"
                              :disabled='disabled'
                              v-model="params.effective_end"
                              placeholder='YYYY/MM/DD'></mrc_ess_datePicker>
        </v-col>
        <v-col cols="12"
               sm="3">
          <v-checkbox v-model="params.is_forward_all_incompleted_items_now"
                      :true-value="1"
                      :readonly="disabled"
                      :label="$t('label_Forward_unapproved_items_to_delegates')+'*'"
                      :false-value="0"></v-checkbox>
        </v-col>
        <v-col cols="12"
               sm="4">
          <v-checkbox v-model="params.is_return_all_incompleted_items_whenexpired"
                      :true-value="1"
                      :readonly="disabled"
                      :label="$t('label_After_the_agent_ends')+'*'"
                      :false-value="0"></v-checkbox>
        </v-col>
      </v-row>
    </v-card-text>
    <mrc_ess_submitBtn @success='success'
                       @cancel='cancel'></mrc_ess_submitBtn>
  </v-card>
</template>

<script>
import { required } from 'vuelidate/lib/validators'
export default {
  components: {},
  props: {
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      params: {
        flow_proxy_type_uuid: '',
        effective_start: '',
        effective_end: '',
        agent_staff_uuid: '',
        proxy_staff_uuid: '',
        is_forward_all_incompleted_items_now: 0,
        is_return_all_incompleted_items_whenexpired: 0
      }
    };
  },
  validations: {
    params: {

    }
  },
  computed: {
    agent_approval_type () {
      return { prompt_name: 'agent_approval_type', org_level_uuid: this.$store.state.userData.org_level_uuid }
    }
  },
  created () { },
  watch: {},
  mounted () { },

  methods: {
    success () {
      this.params.effective_start = this.$moment(this.params.effective_start)
      this.params.effective_end = this.$moment(this.params.effective_end)
      this.$emit('getData', this.params)
    },
    cancel () {
      this.$myConfirm({ content: this.$t('message_Confirm_the_cancellation_of_the_edit') })
        .then(() => {
          this.$router.go(-1)
        }).catch(() => { })
    },
  },
};
</script>

