<template>
  <div>
    <mrc_ess_iconGroup :is_disabled='disabled'
                       @handleReturn='handleReturn'
                       @handleEdit='handleEdit'
                       @handleDel='handleDel'></mrc_ess_iconGroup>
    <approverInfo @getData='getData' />
  </div>
</template>

<script>
import { required } from 'vuelidate/lib/validators'
import approverInfo from './modules/approverInfo'
import { updateApprover, detailApprover } from '@/service/api/approver'
export default {
  components: { approverInfo },
  props: {},
  data () {
    return {
      disabled: true,
      params: {
        uuid: ''
      }
    };
  },
  validations: {},
  computed: {},
  created () { },
  watch: {},
  async mounted () {
    if (!this.$store.state.iconstatus) {
      this.disabled = false
    }
    await this.getDetail()
  },

  methods: {
    handleReturn () {
      this.$router.go(-1)
    },
    async getDetail () {
      const res = await detailApprover({ uuid: this.$store.state.uuid })
      console.log(res);
    },
    handleEdit () {
      this.disabled = !this.disabled
    },
    handleDel () {

    },
    getData (params) {
      console.log(params);
    }
  },
};
</script>

